import MKTypography from "components/MKTypography";

function About() {
  return (
    <>
      <div
        id="about"
        style={{
          justifyContent: "center",
          flexdirection: "column-reverse",
          width: "75%",
          padding: "5px 20px 5px 20px",
          border: "1.5px solid #A0A0A0",
          borderRadius: "10px",
          marginTop: "35px",
          marginBottom: "8px",
          paddingRight: "20px",
          paddingLeft: "30px",
          paddingBottom: "50px",
          marginLeft: "auto",
          marginRight: "auto",
        }}
      >
        <MKTypography
          variant="h3"
          mt={1}
          mb={1}
          sx={({ breakpoints, typography: { size } }) => ({
            [breakpoints.down("md")]: {
              fontSize: size["2xl"],
            },
          })}
          style={{ textAlign: "center" }}
        >
          À propos de nous{" "}
        </MKTypography>

        <p>
          L’état des routes marocaines se détériore. Et avec le froid qui est bien installé, cela
          n’arrange rien : verglas, inondations. Résultat, les routes nationales se dégradent, sans
          compter le passage des poids lourds, et l’entretien des routes par les collectivités qui
          n’est pas optimal. Or cela peut avoir une incidence sur la sécurité routière. Alors, pour
          prévenir les risques, nous somme une équipe des étudiants, nous avons développé
          l’application J’ai mal à ma route. L’objectif est simple : signaler les axes dangereux.
        </p>
      </div>
    </>
  );
}

export default About;
