import { useEffect, useState } from "react";

// react-router components
import { Routes, Route, Navigate, useLocation } from "react-router-dom";

// @mui material components
import { ThemeProvider } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";

// Material Kit 2 React themes
import theme from "assets/theme";

// Material Kit 2 React routes
// import routes from "routes";




import Login from "pages/securityapp/layouts/Auth/Login";
import Register from "pages/securityapp/layouts/Auth/Register";


import ListAlertes from "pages/securityapp/ListAlertes";
import Declare from "pages/securityapp/layouts/Declare";


import Home from "./pages/securityapp/index";

export default function App() {
  const { pathname } = useLocation();
  const [user, setUser] = useState(null);

  // Setting page scroll to 0 when changing the route
  useEffect(() => {
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;
  }, [pathname]);

  // const getRoutes = (allRoutes) =>
  //   allRoutes.map((route) => {
  //     if (route.collapse) {
  //       return getRoutes(route.collapse);
  //     }

  //     if (route.route) {
  //       return <Route exact path={route.route} element={route.component} key={route.key} />;
  //     }

  //     return null;
  //   });
  useEffect(() => {
    const userToken = localStorage.getItem("user")
    console.log(!userToken);
    setUser(userToken)
  }, [])
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Routes>
        
          <Route exact path="/Declare" element={user?<Declare />:<Login/>} />
          <Route exact path="/ListAlertes" element={user?<ListAlertes />:<Login/>} />
          <Route exact path="/login" element={<Login />} />
          <Route exact path="/register" element={<Register />} />
        
        <Route path="/" element={<Home />} />
        <Route path="*" element={<Navigate to={user ? "/" : "/login"} />} />
      </Routes>
    </ThemeProvider>
  );
}
