import { useRef, useState } from "react";
import axios from "axios";

// @mui material components
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import Switch from "@mui/material/Switch";

// Material Kit 2 React components
import MKAlert from "components/MKAlert";
import MKBox from "../../../components/MKBox";
import MKInput from "../../../components/MKInput";
import MKButton from "../../../components/MKButton";
import MKTypography from "../../../components/MKTypography";



function Contacter() {
  const nom = useRef('')
  const prenom = useRef('')
  const message = useRef('')
  const email = useRef('')
  const [ok, setOk] = useState(null)
  const [checked,setChecked] = useState(true);
  const handleChecked=()=>setChecked(!checked);
  

  const submit = () => {
    const formData = new FormData();
    formData.append("nom", nom.current.value);
    formData.append("prenom", prenom.current.value);
    formData.append("message", message.current.value);
    formData.append("email", email.current.value);
    const headers = {
      "x-access-token": localStorage.getItem("user")
    }
    axios.post("https://the-safe-way.herokuapp.com//contact", formData, { headers }).then(() => {
      setOk(true)
    }).catch(() => {
      setOk(false)
    })

  };

  return (
    <div id="contact">
      <MKBox component="section" py={12}>
        <Container>
          <Grid container item justifyContent="center" xs={10} lg={7} mx="auto" textAlign="center">
            <MKTypography variant="h3" mb={1}>
              Contactez-nous
            </MKTypography>
          </Grid>


          {ok === true ?
            <MKAlert mx="auto" width="50%" mt={2} color="success" dismissible> You email sent successfully

            </MKAlert> : <></>}
          {ok === false ? <MKAlert mx="auto" width="50%" mt={2} color="error" dismissible>Oops something is wrong

          </MKAlert> : <></>}

          <Grid container item xs={12} lg={7} sx={{ mx: "auto" }}>
            <MKBox width="100%" component="form" method="post" autocomplete="off">
              <MKBox p={3}>
                <Grid container spacing={3}>
                  <Grid item xs={12} md={6}>
                    <MKInput ref={nom} variant="standard" label="Nom" fullWidth />
                  </Grid>
                  <Grid item xs={12} md={6}>
                    <MKInput ref={prenom} variant="standard" label="Prénom" fullWidth />
                  </Grid>
                  <Grid item xs={12}>
                    <MKInput ref={email} variant="standard" type="email" label="Email" fullWidth />
                  </Grid>
                  <Grid item xs={12}>
                    <MKInput
                      ref={message}
                      variant="standard"
                      label="votre message"
                      multiline
                      fullWidth
                      rows={6}
                    />
                  </Grid>
                  <Grid item xs={12} alignItems="center" ml={-1}>
                    <Switch checked={checked} onChange={handleChecked} />
                    <MKTypography
                      variant="button"
                      fontWeight="regular"
                      color="text"
                      ml={-1}
                      sx={{ cursor: "pointer", userSelect: "none" }}
                      onClick={handleChecked}
                    >
                      &nbsp;&nbsp;j’accepte&nbsp;
                    </MKTypography>
                    <MKTypography
                      component="a"
                      href="#"
                      variant="button"
                      fontWeight="regular"
                      color="dark"
                    >
                      les conditions
                    </MKTypography>
                  </Grid>
                </Grid>
                <Grid container item justifyContent="center" xs={12} my={2}>
                  <MKButton
                    onClick={() => {
                      submit();
                    }}
                    variant="gradient"
                    color="dark"
                    fullWidth
                  >
                    Envoyer
                  </MKButton>
                </Grid>
              </MKBox>
            </MKBox>
          </Grid>
        </Container>
      </MKBox>
    </div>
  );
}

export default Contacter;
