import PropTypes from "prop-types";

// @mui material components
import Link from "@mui/material/Link";
import Grid from "@mui/material/Grid";
import Stack from "@mui/material/Stack";

// @mui icons
import FacebookIcon from "@mui/icons-material/Facebook";
import InstagramIcon from "@mui/icons-material/Instagram";
import LinkedinIcon from "@mui/icons-material/LinkedIn";
import GitHubIcon from "@mui/icons-material/GitHub";

// Material Kit 2 React components
import MKBox from "../../../components/MKBox";
import MKTypography from "../../../components/MKTypography";

function Footer({ company, socials, light }) {
  const { href, name } = company;

  const year = new Date().getFullYear();

  const renderSocials = socials.map((social) => (
    <MKTypography
      key={social.link}
      component={Link}
      href={social.link}
      variant="body2"
      color={light ? "white" : "secondary"}
      fontWeight="regular"
    >
      {social.icon}
    </MKTypography>
  ));

  return (
    <MKBox component="footer" py={6}>
      <Grid container justifyContent="center">
        <Grid item xs={12} lg={8}>
          <Stack display="flex" direction="row" justifyContent="center" spacing={3} mt={1} mb={3}>
            {renderSocials}
          </Stack>
        </Grid>
        <Grid item xs={12} lg={8} sx={{ textAlign: "center" }}>
          <MKTypography variant="body2" color={light ? "white" : "secondary"}>
            Copyright &copy; {year} created by{" "}
            <MKTypography
              component={Link}
              href={href}
              target="_blank"
              rel="noreferrer"
              variant="body2"
              color={light ? "white" : "secondary"}
            >
              {name}
            </MKTypography>
            .
          </MKTypography>
        </Grid>
      </Grid>
    </MKBox>
  );
}

// Setting default values for the props of CenteredFooter
Footer.defaultProps = {
  company: { href: "#", name: "Our Team" },
  socials: [
    {
      icon: <FacebookIcon fontSize="small" />,
      link: "https://web.facebook.com/ibrahim.elkhaddi.1"
    },
    {
      icon: <InstagramIcon fontSize="small" />,
      link: "https://www.instagram.com/idis.jpg/",
    },
    {
      icon: <LinkedinIcon fontSize="small" />,
      link: "https://www.linkedin.com/in/oumaima-bougryne-1a98191b4",
    },
    { icon: <GitHubIcon fontSize="small" />, link: "https://github.com/oumaimaelb" },
  ],
  light: false,
};

// Typechecking props for the CenteredFooter
Footer.propTypes = {
  company: PropTypes.objectOf(PropTypes.string),
  socials: PropTypes.arrayOf(PropTypes.object),
  light: PropTypes.bool,
};

export default Footer;
