import { useEffect, useRef, useState } from "react";

import axios from "axios";

// @mui material components
import Card from "@mui/material/Card";
// import Switch from "@mui/material/Switch";
import Grid from "@mui/material/Grid";
// import MuiLink from "@mui/material/Link";
import { Icon, Menu, MenuItem } from "@mui/material";


import MKAlert from "components/MKAlert";


// Material Kit 2 React components
import MKBox from "../../../components/MKBox";
import MKTypography from "../../../components/MKTypography";
import MKInput from "../../../components/MKInput";
import MKButton from "../../../components/MKButton";

// Material Kit 2 React example components
import DefaultNavbar from "../../../examples/Navbars/DefaultNavbar";

// Material Kit 2 React page layout routes
// import routes from "../../../routes";

// Images
import bgImage from "../../../assets/images/bg-sign-in-basic.jpeg";
import Home from "..";



function Declare() {
    // const [checked, setChecked] = useState(true);

    const [dropdown, setDropdown] = useState(null);
    const [type,setType] = useState("Signalisation");
    const [coord, setCoord] = useState({ "lat": null, "lng": null})
    const [ok,setOk] = useState(null)
    const description = useRef("")
    const padTo2Digits = (num) => num.toString().padStart(2, '0');
    
    const formatDate =(date)=>[padTo2Digits(date.getMonth() + 1),padTo2Digits(date.getDate()),date.getFullYear(),].join('-');
    

  const submit =()=>{
    const formData = new FormData();
    formData.append("type",type);
    formData.append("lat",coord.lat);
    formData.append("lng",coord.lng);
    formData.append("status","new");
    formData.append("description",description.current.value);
    formData.append("date",formatDate(new Date()));
    console.log(formData);
    const headers = {
      "x-access-token":localStorage.getItem("user")
    }
    axios.post(`https://the-safe-way.herokuapp.com//declare`,formData,{headers}).then((res)=>{
      console.log(res.data);
      setOk(true)
    }).catch(()=>{
      setOk(false)
    })
  }
  
    const routes = [
        {
          name: "home",
          route: "/home",
          component: <Home />,
        },
        {
          name: "Signaler une alerte",
          route: "/declarer",
          component: <Home />,
        },
        {
          name: "demander de l'aide",
          route: "/aide",
          component: <Home />,
        },
      ];
    
  
    const openDropdown = ({ currentTarget }) => setDropdown(currentTarget);
    const closeDropdown = () => setDropdown(null);

    useEffect(() => {
        navigator.geolocation.getCurrentPosition((position) => {
        setCoord({ lat: position.coords.latitude, lng: position.coords.longitude });
        });

    }, [])

  
  
  
    // Styles
    const iconStyles = {
      ml: 1,
      fontWeight: "bold",
      transition: "transform 200ms ease-in-out",
    };
  
    const dropdownIconStyles = {
      transform: dropdown ? "rotate(180deg)" : "rotate(0)",
      ...iconStyles,
    };
  return (
    <>
      <DefaultNavbar
        routes={routes}
        transparent
        light
      />
      <MKBox
        height="110vh"
        sx={{
          backgroundImage: ({ functions: { linearGradient, rgba }, palette: { gradients } }) =>
            `${linearGradient(
              rgba(gradients.dark.main, 0.6),
              rgba(gradients.dark.state, 0.6)
            )}, url(${bgImage})`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          backgroundRepeat: "no-repeat",
        }}
      >
        
          <MKBox px={1} width="100%"  mx="auto" mb={20} >
        <Grid container spacing={1} justifyContent="center" alignItems="center" >
        
          <Grid mt={15} item xs={11}  md={6} >
            
            <Card>
            
              <MKBox
                height="50px"
                width="50%"
                variant="gradient"
                bgColor="info"
                borderRadius="lg"
                coloredShadow="info"
                mx="auto"
                mt={-3}
                textAlign="center"
              >
                <MKTypography variant="h5" fontWeight="medium" color="white" style={{lineHeight:"50px"}} >
                     Déclarer une Alerte
                </MKTypography>
               
              </MKBox>
              {ok===true?
        <MKAlert  mx="auto" width="80%" mt={2} color="success" dismissible> Your Alert is sent !
        
        </MKAlert>:<></>}
        {ok===false?<MKAlert  mx="auto" width="80%" mt={2} color="error" dismissible>Oops something is wrong 
        
        </MKAlert>:<></>}
              <MKBox pt={4} pb={3} px={3}>
                <MKBox component="form" role="form">
                <MKBox p={3}>
                <Grid>
                <div style={{width:"100%"}}>
                    <MKTypography style={{color:"gray",fontSize:"14px"}} mb={2}>Type </MKTypography>
                        <MKButton fullWidth  style={{bodrer:"info",background:"#DBE8F2",textAlign:"left"}} onClick={openDropdown}>
                             {type} <Icon sx={dropdownIconStyles}>expand_more</Icon>
                        </MKButton>
                        <Menu anchorEl={dropdown} open={Boolean(dropdown)} onClose={closeDropdown}>
                            <MenuItem onClick={()=>setType("Accident")}>Accident</MenuItem>
                            <MenuItem onClick={()=>setType("Aménagements de sécurité")}>Aménagements de sécurité</MenuItem>
                            <MenuItem onClick={()=>setType("Inondations")}>Inondations</MenuItem>
                            <MenuItem onClick={()=>setType("Obstacle")}>Obstacle</MenuItem>
                            <MenuItem onClick={()=>setType("Signalisation")}>Signalisation</MenuItem>
                            <MenuItem onClick={()=>setType("Autres")}>Autres</MenuItem>
                        </Menu>
                </div>
                <Grid item xs={12} mt={2}>
                  <MKInput ref={description} variant="standard" label="Description" multiline fullWidth rows={6} />
                </Grid>
              </Grid>
              <Grid container item justifyContent="center" xs={12} my={2}>
                <MKButton onClick={()=>submit()} variant="gradient" color="dark" fullWidth>
                  Déclarer
                </MKButton>
              </Grid>
            </MKBox>   
                </MKBox>
              </MKBox>
            </Card>
          </Grid>
        </Grid>
      </MKBox>
      </MKBox>
    </>
  );
}

export default Declare;
