import { useEffect, useState } from 'react';
import { GoogleMap, LoadScript, Marker } from '@react-google-maps/api';
import axios from 'axios';
import MKAlert from 'components/MKAlert';
import location from "../../assets/images/location.png";
import danger from "../../assets/images/danger_location.png"


const MyMapComponent = () => {
  const [coord, setCoord] = useState({ "lat": 34.0333332, "lng": -6.83333 })
  const [list,setList] = useState([{}])
  const containerStyle = {
    width: '100%',
    height: '90vh'
  };
  
  

  useEffect(() => {
    navigator.geolocation.getCurrentPosition((position) => {
      setCoord({ lat: position.coords.latitude, lng: position.coords.longitude });
    });
  }, [])

  useEffect(()=>{
    const headers = {
      "x-access-token":localStorage.getItem("user")
    }
    axios.get("https://the-safe-way.herokuapp.com//get_declares", {headers}).then(res=>{
      console.log(res.data.localisations);
      setList(res.data.localisations)
    })
  },[])

  return (
    <LoadScript
      googleMapsApiKey="AIzaSyBvhnIGIwiLEYRc6nZxKPpfpTisyZgADHQ"
    >
      <div style={{ position: "absolute", display: "flex", alignItems: "center", bottom: "10px", left: "10px", padding: "10px", textAlign: "center", minWidth: "20%", borderRadius: '10px', zIndex: "1000" }}>
        {list.length!==0?
          <MKAlert color="error" dismissible>Vérifiez la carte, il y a du danger sur la route !
        
        </MKAlert>:<></>}
      </div>
      <GoogleMap
        mapContainerStyle={containerStyle}
        center={{
          lat: coord.lat,
          lng: coord.lng
        }}
        
        zoom={15}
      >
        <Marker icon={location} position={{lat: coord.lat,lng: coord.lng}} animation="DROP"/>
        {list.map((ele,index) =><Marker icon={danger} key={String(index)} label="Danger"  position={{lat: ele.lat,lng: ele.lng}} animation="DROP"/> )}

      </GoogleMap>

    </LoadScript>
  )
}


export default MyMapComponent