import DefaultNavbar from "examples/Navbars/DefaultNavbar";
import MyMapComponent from "components/mapCompnent/index";


// Routes
import Home from ".";
import MKBox from "../../components/MKBox";
import Declare from "./layouts/Declare";

function ListAlertes() {
  // const user = null;

  const routes = [
    {
      name: "home",
      route: "/home",
      component: <Home />,
    },
    {
      name: "Signaler une alerte",
      route: "/declare",
      component: <Declare />,
    },
    // {
    //   name: "demander de l'aide",
    //   route: "/aide",
    //   component: <Home />,
    // },
  ];
  return (
    <>
      <MKBox variant="gradient" bgColor="dark" shadow="sm" py={0.25}>
        <DefaultNavbar   routes={routes} transparent relative light />
      </MKBox>
      <div style={{ height: "80vh", width: "100%"}}>
       <MyMapComponent/>
      </div>
    </>
  );
}

export default ListAlertes;
