// @mui material components
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import Link from "@mui/material/Link";
import Stack from "@mui/material/Stack";

// Material Kit 2 React components
import MKBox from "../../components/MKBox";
import MKButton from "../../components/MKButton";
import MKTypography from "../../components/MKTypography";

// Images
import bgImage from "../../assets/images/bgimage.jpeg";
import About from "./sections/about";
import Contacter from "./sections/contact";
import Footer from "./sections/footer";

function Home() {
  return (
    <>
      <MKBox component="header" position="relative">
        <MKBox component="nav" position="absolute" top="0.5rem" width="100%">
          <Container>
            <Grid
              container
              justifyContent="space-around"
              flexDirection="row"
              alignItems="center"
            >
              <MKButton
                variant="outlined"
                color="white"
                sx={{ display: { xs: "block", lg: "none" }, ml: "auto" }}
              >
                <MKBox component="i" color="white" className="fas fa-bars" />
              </MKButton>
              <MKBox
                component="ul"
                display={{ xs: "none", lg: "flex" }}
                p={0}
                my={0}
                mx="auto"
                sx={{ listStyle: "none" }}
                style={{ justifyContent: "space-around", width: "30%" }}
              >
                <MKBox component="li">
                  <MKTypography
                    component={Link}
                    href="#"
                    variant="button"
                    color="white"
                    fontWeight="regular"
                    p={1}
                    onClick={(e) => e.preventDefault()}
                  >
                    Accuiel
                  </MKTypography>
                </MKBox>
                <MKBox component="li">
                  <a
                    style={{ color: "white", padding: "1px", fontSize: "14px" }}
                    href="#about"
                  >
                    À propos de nous
                  </a>
                </MKBox>
                <MKBox component="li">
                  <a
                    style={{ color: "white", padding: "1px", fontSize: "14px" }}
                    href="#contact"
                  >
                    Contactez-nous
                  </a>
                </MKBox>
              </MKBox>
            </Grid>
          </Container>
        </MKBox>
        <MKBox
          display="flex"
          alignItems="center"
          minHeight="100vh"
          sx={{
            backgroundImage: ({
              palette: { gradients },
              functions: { linearGradient, rgba },
            }) =>
              `${linearGradient(
                rgba(gradients.dark.main, 0.5),
                rgba(gradients.dark.state, 0.5)
              )}, url(${bgImage})`,
            backgroundSize: "cover",
            backgroundPosition: "center",
          }}
        >
          <Container>
            <Grid
              container
              item
              xs={12}
              md={7}
              lg={6}
              flexDirection="column"
              justifyContent="center"
            >
              <MKTypography
                variant="h1"
                color="white"
                mb={3}
                sx={({ breakpoints, typography: { size } }) => ({
                  [breakpoints.down("md")]: {
                    fontSize: size["3xl"],
                  },
                })}
              >
                J’AI MAL À MA ROUTE
              </MKTypography>
              <MKTypography
                variant="body1"
                color="white"
                opacity={0.8}
                pr={6}
                mr={6}
              >
                si vous connaissez une route délabrée ou même dangereuse près de
                chez vous, n’hésitez pas à le signaler
              </MKTypography>
              <Stack direction="row" spacing={1} mt={3}>
                <MKButton href="/login" color="white">
                  Se connecter
                </MKButton>
                <MKButton href="/register" color="white">
                  S’inscrire
                </MKButton>
              </Stack>
            </Grid>
          </Container>
        </MKBox>
      </MKBox>
      <About />
      <Contacter />
      <Footer />
    </>
  );
}
export default Home;
